<template>
  <v-sheet class="pa-2" border rounded="lg" :height="height">
    <MapboxMap
      ref="mapboxMap"
      :accessToken="accessToken"
      mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
      @mb-created="(mapInstance) => (map = mapInstance)"
      :center="mapCenter"
      :zoom="zoom"
      :style="{ height: '100%' }"
    ></MapboxMap>
  </v-sheet>
</template>

<script>
import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

export default {
  name: "RegionMap",
  components: { MapboxMap },
  props: {
    region: Object,
    zoom: {
      type: Number,
      default: 16,
    },
    height: {
      type: String,
      default: "100%",
    },
    width: {
      type: String,
      default: "100%",
    },
  },
  setup() {
    const popup = new mapboxgl.Popup({
      closeButton: true,
      closeOnClick: true,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { popup, accessToken };
  },
  data() {
    return {
      map: null,
    };
  },
  computed: {
    mapCenter() {
      const cordinates = this.region.geometry.geometry_shape.coordinates[0];
      const sum = cordinates.reduce(
        (acc, [lng, lat]) => [acc[0] + lng, acc[1] + lat],
        [0, 0]
      );
      const center = [sum[0] / cordinates.length, sum[1] / cordinates.length];
      return center;
    },
    regionShape() {
      return {
        type: "Feature",
        properties: {
          region_name: this.region.region_name,
          region_code: this.region.region_code,
        },
        geometry: this.region.geometry.geometry_shape,
      };
    },
    validEois() {
      return this.region.elements_of_interest.filter((eoi) => eoi.geometry);
    },
    eoisShape() {
      return {
        type: "FeatureCollection",
        features: this.validEois.map((eoi) => ({
          type: "Feature",
          properties: {
            eoi_id: eoi.eoi_id,
            eoi_name: eoi.eoi_name || "Sem nome",
            eoi_type: eoi.eoi_type,
          },
          geometry: eoi.geometry,
        })),
      };
    },
    stationShape() {
      if (!this.region.closest_datasource) return null;
      return {
        type: "Feature",
        properties: {
          station_name: this.region.closest_datasource?.datasource_name,
          station_provider: this.region.closest_datasource?.dataprovider,
        },
        geometry: this.region.closest_datasource?.datasource_location,
      };
    },
  },
  methods: {
    getCenter(coordinates) {
      const sum = coordinates.reduce(
        (acc, [lng, lat]) => [acc[0] + lng, acc[1] + lat],
        [0, 0]
      );
      return [sum[0] / coordinates.length, sum[1] / coordinates.length];
    },
    flyToStation() {
      this.map.flyTo({
        center: this.stationShape.geometry.coordinates,
        zoom: 16,
      });
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("region", {
        type: "geojson",
        data: this.regionShape,
      });

      // REGION //
      this.map.addLayer({
        id: "region-polygon",
        type: "fill",
        source: "region",
        paint: {
          "fill-color": "#088",
          "fill-opacity": 0.4,
        },
        filter: ["==", "$type", "Polygon"],
      });
      // REGION POLYGON //
      this.map.on("mouseenter", "region-polygon", () => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("click", "region-polygon", (e) => {
        console.log(e);
        this.map.getCanvas().style.cursor = "pointer";
        const description = `Nome: <b>${e.features[0].properties.region_name}</b><br> Código: <b>${e.features[0].properties.region_code}</b>`;
        this.popup.setLngLat(e.lngLat).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "region-polygon", () => {
        this.map.getCanvas().style.cursor = "";
      });

      // EOIs //
      this.map.addSource("eois", {
        type: "geojson",
        data: this.eoisShape,
      });
      this.map.addLayer({
        id: "eois-points",
        type: "circle",
        source: "eois",
        paint: {
          "circle-radius": 5,
          "circle-color": "#880",
          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
        filter: ["==", "$type", "Point"],
      });
      this.map.addLayer({
        id: "eois-lines",
        type: "line",
        source: "eois",
        paint: {
          "line-color": "#880",
          "line-width": 2,
        },
        filter: ["==", "$type", "LineString"],
      });
      this.map.addLayer({
        id: "eois-polygons",
        type: "fill",
        source: "eois",
        paint: {
          "fill-color": "#880",
          "fill-opacity": 0.4,
        },
        filter: ["==", "$type", "Polygon"],
      });
      // EOIs POINTS //
      this.map.on("mouseenter", "eois-points", () => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("click", "eois-points", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        const description = `Nome: <b>${e.features[0].properties.eoi_name}</b><br>Tipo: <b>${e.features[0].properties.eoi_type}</b>`;
        this.popup.setLngLat(e.lngLat).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "eois-points", () => {
        this.map.getCanvas().style.cursor = "";
      });

      // EOIs LINES //
      this.map.on("mouseenter", "eois-lines", () => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("click", "eois-lines", (e) => {
        console.log(e.features[0].properties);
        this.map.getCanvas().style.cursor = "pointer";
        const description = `Nome: <b>${e.features[0].properties.eoi_name}</b><br>Tipo: <b>${e.features[0].properties.eoi_type}</b>`;
        this.popup.setLngLat(e.lngLat).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "eois-lines", () => {
        this.map.getCanvas().style.cursor = "";
      });

      // EOIs POLYGONS //
      this.map.on("mouseenter", "eois-polygons", () => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("click", "eois-polygons", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        const description = `Nome: <b>${e.features[0].properties.eoi_name}</b><br>Tipo: <b>${e.features[0].properties.eoi_type}</b>`;
        this.popup.setLngLat(e.lngLat).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "eois-polygons", () => {
        this.map.getCanvas().style.cursor = "";
      });

      // STATION //
      if (!this.stationShape) return;
      this.map.addSource("station", {
        type: "geojson",
        data: this.stationShape,
      });
      this.map.addLayer({
        id: "station-point",
        type: "circle",
        source: "station",
        paint: {
          "circle-radius": 10,
          "circle-color": "#f00",
          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
        filter: ["==", "$type", "Point"],
      });
      this.map.on("mouseenter", "station-point", () => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("click", "station-point", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        const description = `Estação: <b>${e.features[0].properties.station_name}</b><br>Provedor: <b>${e.features[0].properties.station_provider}</b>`;
        this.popup.setLngLat(e.lngLat).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "station-point", () => {
        this.map.getCanvas().style.cursor = "";
      });
    });
  },
};
</script>

<style></style>
