<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn
        v-bind="activatorProps"
        color="info"
        size="small"
        variant="text"
        icon
      >
        <v-icon>mdi-chart-line</v-icon>
        <v-tooltip activator="parent" location="bottom">
          Dados recolhidos
        </v-tooltip>
      </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card width="900" height="800"
        ><v-card-text class="pb-0">
          <v-container
            :class="{ 'fill-height': isLoading || series.length == 0 }"
          >
            <v-row v-if="isLoading" align="center">
              <v-col cols="12" class="d-flex align-center justify-center"
                ><v-progress-circular
                  color="secondary"
                  size="65"
                  width="7"
                  indeterminate
                ></v-progress-circular>
              </v-col>
            </v-row>
            <v-row v-else-if="!isLoading && series.length == 0">
              <v-col>
                <v-card class="fill-width" elevation="0">
                  <v-card-text class="text-center">
                    <v-icon size="100">mdi-database-off</v-icon>
                    <h2 class="text-h5">
                      Sem informação para esta estação nos últimos 7 dias
                    </h2>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
            <div v-else>
              <v-row>
                <v-col cols="12">
                  <v-card border rounded>
                    <v-card-title>
                      <span class="text-h6"
                        >Selecione no máximo {{ maxSets }} tipos de dados</span
                      >
                    </v-card-title>
                    <v-card-text class="scrollable-card py-2 px-5">
                      <v-row no-gutters>
                        <v-col
                          cols="6"
                          v-for="(col, index) in variablesColumns"
                          :key="index"
                        >
                          <v-switch
                            v-for="serie in variablesColumns[index]"
                            :key="serie.series_variable.seriesvariable_id"
                            v-model="dataSets"
                            :readonly="
                              hasMaxedSets && !dataSets.includes(serie)
                            "
                            :label="
                              serie.series_variable.seriesvariable_longname
                            "
                            :color="
                              dataSets.includes(serie)
                                ? getColor(serie)
                                : 'info'
                            "
                            density="compact"
                            :value="serie"
                            hide-details
                          ></v-switch>
                        </v-col>
                      </v-row>
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-row>
              <br />
              <span class="text-h6">Dados recolhidos nos últimos 7 dias</span>
              <br />
              <canvas id="chart"></canvas>
            </div> </v-container></v-card-text
      ></v-card>
    </template>
  </v-dialog>
</template>

<script>
import Chart from "chart.js/auto";
import "chartjs-adapter-date-fns";
import zoomPlugin from "chartjs-plugin-zoom";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

import { shallowRef } from "vue";

Chart.register(zoomPlugin);

export default {
  name: "StationData",
  props: {
    station_id: {
      type: Number,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      isLoading: true,
      series: [],

      mixedChart: null,
      dataSets: [],
      maxSets: 3,
      colors: ["#B00020", "#00574b", "#2196F3"],
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
    dataSets(val) {
      if (this.mixedChart) {
        console.log(this.chartData.datasets);
        this.mixedChart.data.datasets = this.chartData.datasets;
        this.mixedChart.options = this.options;
        this.mixedChart.update();
      }
    },
  },
  computed: {
    lastWeekDate() {
      const date = new Date();
      date.setDate(date.getDate() - 7);
      return date.toISOString().split("T")[0];
    },
    lastWeekDates() {
      const dates = [];
      const today = new Date();
      for (let i = 6; i >= 0; i--) {
        const date = new Date(today);
        date.setDate(today.getDate() - i);
        dates.push(date.toISOString().split("T")[0]);
      }
      return dates;
    },
    todayDate() {
      return new Date().toISOString().split("T")[0];
    },
    variablesColumns() {
      const midIndex = Math.ceil(this.series.length / 2);
      return [this.series.slice(0, midIndex), this.series.slice(midIndex)];
    },
    hasMaxedSets() {
      return this.dataSets.length >= this.maxSets;
    },
    // CHART
    chartData() {
      let _datasets = [];
      this.dataSets.forEach((dataSet, index) => {
        let _data = [];
        dataSet.series_data.forEach((data) => {
          _data.push({
            x: data.seriesdata_timestamp,
            y: data.seriesdata_realvalue,
          });
        });
        // const randomColor = Math.floor(Math.random() * 16777215).toString(16);
        _datasets.push({
          type: "line",
          label: dataSet.series_variable.seriesvariable_longname,
          backGroundColor: this.colors[index],
          borderColor: this.colors[index],
          borderWidth: 1,
          stacK: "combined",
          pointRadius: 1,
          data: _data,
          yAxisID: "y" + dataSet.series_id,
        });
      });
      return {
        labels: [],
        datasets: _datasets,
      };
    },
    options() {
      let yAxes = [];
      yAxes.push({
        id: "x",
        type: "time",
        min: this.lastWeekDate + " 00:00",
        max: this.todayDate + " 23:59",
        time: {
          unit: "minute",
          tootipFormat: "yyyy-MM-dd HH:mm",
          displayFormats: {
            minute: "dd/MM",
          },
        },
        ticks: {
          stepSize: 60 * 24,
          fontColor: "black",
          fontSize: 11,
          fontStyle: "bold",
          labelOffset: 20,
          source: "auto",
        },
        display: true,
        title: {
          display: true,
          text: "Data",
        },
      });
      this.dataSets.forEach((dataSet, index) => {
        yAxes.push({
          id: "y" + dataSet.series_id,
          type: "linear",
          display: true,
          position: "left",
          scaleLabel: {
            display: true,
            labelString: dataSet.series_variable.seriesvariable_longname,
          },
          ticks: {
            beginAtZero: true,
          },
          title: {
            display: true,
            color: this.colors[index],
            text: dataSet.series_variable.unit_symbol,
          },
        });
      });
      let _scales = {};
      yAxes.forEach((axis) => {
        _scales[axis.id] = axis;
      });
      return {
        responsive: true,
        scales: _scales,
        plugins: {
          zoom: {
            pan: {
              enabled: true,
              mode: "xy",
            },
            zoom: {
              wheel: {
                enabled: true,
              },
              pinch: {
                enabled: true,
              },
              mode: "xy",
            },
          },
        },
      };
    },
  },
  methods: {
    onOpenDialog() {
      this.getStationData();
    },
    reset() {
      this.series = [];
      this.dataSets = [];
      if (this.mixedChart) this.mixedChart.clear();
    },
    async getStationData() {
      this.isLoading = true;
      await axios
        .request({
          url: `api/datasources/${this.station_id}/data`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          params: {
            start_date: this.lastWeekDate,
            end_date: this.todayDate,
          },
        })
        .then((response) => {
          this.series = response.data;
          this.isLoading = false;
          if (this.series.length > 0) {
            this.$nextTick(() => {
              this.loadChart();
            });
          }
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao buscar dados da estação.");
          this.dialog = false;
        });
    },
    loadChart() {
      let ctx = document.getElementById("chart");

      this.mixedChart = shallowRef(
        new Chart(ctx, {
          options: this.options,
        })
      );
    },
    getColor(serie) {
      return this.colors[this.dataSets.indexOf(serie)];
    },
  },
};
</script>

<style>
.scrollable-card {
  max-height: 220px;
  width: 100%;
  overflow-y: auto;
}
</style>
